import React from 'react'
import { Container, Typography, Button, Grid} from '@material-ui/core'
import CartItem from './CartItem/CartItem'
import useStyles from './styles'
import {Link} from 'react-router-dom'

const Cart = ({cart, handleEmptyCart, handleUpdateCart, handleRemoveFromCart}) => {
    const classes = useStyles()
    if(cart.line_items === undefined){
        return( <div>loading</div>)
    }
    // const isEmpty = true
    const isEmpty = cart.line_items === undefined? true : !cart.line_items.length
    
    

    const EmptyCart = () => {
        return(
            <Typography variant="subtitle1"> 
                You have no items in your shopping cart, 
                <Link to='/' className={classes.link}>start adding some</Link>
            </Typography>
        )
    } 

    const FilledCart = () => {

        if(cart.line_items === undefined){
            return( <div>loading</div>)
        }

        return(
            <>
                <Grid container spacing={3}>
                    {
                        cart.line_items.map((item) => (
                            <Grid item xs={12} sm={4} key={item.id}>
                                <CartItem item={item} handleRemoveFromCart={handleRemoveFromCart} handleUpdateCart={handleUpdateCart} />
                                
                            </Grid>
                         
                        ))
                    }
                </Grid>
                <div className="classes.cardDetails">
                    <Typography variant="h4"> 
                        subtotal : {cart.subtotal.formatted_with_symbol}
                    </Typography>
                    <div>
                        <Button className={classes.emptyButton} size='large' type='button' variant='contained' color='secondary' onClick={handleEmptyCart}>
                            Empty Cart
                        </Button>
                        <Button className={classes.checkoutButton} size='large' type='button' variant='contained' color='primary'>
                            Checkout
                        </Button>
                    </div>
                </div>
            </>
        )
    }


    return (
        <Container>
            <div className={classes.toolbar} />
            <Typography className={classes.title} variant='h3' gutterBottom>
                Your Shopping Cart
            </Typography>
            {isEmpty? <EmptyCart /> : <FilledCart />}
        </Container>
    )
}

export default Cart
